.portfolio-hero {
  background: url("../img/hero_background.webp") no-repeat top center/cover, url("../img/hero_background.jpg") no-repeat top center/cover;
}
.portfolio-hero__content {
  padding-top: 234px;
  padding-bottom: 234px;
}
.portfolio-hero__content .content__top {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 30px;
}
.portfolio-hero__content .content__top-title {
  font-family: "RubikMonoOne";
  font-style: normal;
  font-weight: 400;
  font-size: 45px;
  line-height: 56px;
  color: var(--light-color);
  max-width: 210px;
}
.portfolio-hero__content .content__top-text {
  width: 696px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 29px;
  color: var(--light-color);
}
.portfolio-hero__content .content__bottom {
  margin-top: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
  width: fit-content;
}